import React, { Fragment } from 'react';
import { Anchor, Ellipse, Cylinder, Box, Hemisphere, Rect, RoundedRect, Group, Shape, Polygon } from 'react-zdog';

import { AmpShape } from '../shapes/letters';

const DARK_AQUA = "#84DCC6";
const LIGHT_AQUA = "#A5FFD6";
const LIGHT_RED = "#FFA69E";
const DARK_RED = "#FF686B";
const YELLOW = "#FFCE49";

const ShapeExtrusion = ({ fill=true, color="#333", sideColor="#BBB", depth=30, spacing=1.1, stroke=1, sideStroke, ...props }) => {
    if (typeof spacing === 'number') {
        spacing = { x: 0, y: 0, z: spacing }
    }

    if (typeof sideColor !== "function") {
        sideColor = (i) => sideColor;
    }

    return (
        <Anchor>
            <Group>
                {[...Array(depth)].map((n, i) => (
                    <AmpShape
                        color={ i === (depth - 1)? color : sideColor(i) }
                        translate={{ x: i * spacing.x, y: i * spacing.y, z: i * spacing.z }}
                        fill={ fill && (i === 0 || i === (depth - 1)) }
                        stroke={ i === (depth - 1)? stroke : (sideStroke? sideStroke : stroke) }
                        {...props} />
                ))}
            </Group>
        </Anchor>
    );
}

export default ShapeExtrusion;